<template>
    <div class="buttons__row">
        <CalcButton class="buttons__row-button" v-for="number in numbers" 
                    :key="number" @click="change(number)">{{ number }}</CalcButton>
    </div>
</template>

<script>
import CalcButton from './CalcButton.vue';
import { mapMutations } from 'vuex';

export default {
    props: {
        numbers: {
            type: Array,
            required: true
        }
    },
    components: { CalcButton },
    methods: {
        ...mapMutations({
            change: 'change',
        }),
    },
}
</script>

<style lang="scss" scoped>
    .buttons__row{
        display: flex;
        align-items: center;
        &-button{
            &:not(:last-of-type){
                margin-right: 10px;
            }
        }
    }
</style>