<template>
    <button class="button" :class="{red: isRed, orange: isOrange}">
        <slot></slot>
    </button>
</template>

<script>
export default {
    props: {
        isRed: {
            type: Boolean,
            default: false
        },
        isOrange: {
            type: Boolean,
            default: false
        }
    }
}
</script>

<style lang="scss" scoped>
    .button{
        outline: none;
        background: white;
        border: none;
        box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.493);
        transition: .1s ease all;
        padding: 10px 12px;
        border-radius: 8px;
        cursor: pointer;
        user-select: none;
        &:hover{
            transform: translateY(-1px);
        }
        &:active{
            transform: translateY(3px);
            background: rgb(241, 171, 16);
            box-shadow: 0 1px 0 1px rgb(158, 111, 9);
            color: white;
        }
        &.red{
            background: rgb(235 0 0);
            color: white;
            box-shadow: 0 1px 0 1px rgb(109, 0, 0);
        }
        &.orange{
            background: rgb(241, 171, 16);
            box-shadow: 0 1px 0 1px rgb(158, 111, 9);
            color: white;
        }
    }
</style>