<template>
    <div class="wrapper">
        <div class="calculator">
            <div class="calculator__top">
                <CalcTotal class="calculator__total" />
            </div>
            <div class="calculator__between">
                <CalcButton class="calculator__between-el calculator__clear" :isRed="true" @click="clear">C</CalcButton>
                <CalcButton class="calculator__between-el calculator__end" :isOrange="true" @click="equals">=</CalcButton>
            </div>
            <div class="calculator__bottom">
                <div class="calculator__bottom-left">
                    <ButtonRow class="calculator__bottom-el" :numbers="[7,8,9]" />
                    <ButtonRow class="calculator__bottom-el" :numbers="[4,5,6]" />
                    <ButtonRow class="calculator__bottom-el" :numbers="[1,2,3]" />
                    <ButtonRow class="calculator__bottom-el calculator__bottom-long" :numbers="[0]" />
                </div>
                <div class="calculator__bottom-right">
                    <CalcButton @click="addSign('+')">+</CalcButton>
                    <CalcButton @click="addSign('*')">*</CalcButton>
                    <CalcButton @click="addSign('-')">-</CalcButton>
                    <CalcButton @click="addSign('/')">/</CalcButton>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ButtonRow from '@/components/ButtonRow.vue';
import { mapMutations } from 'vuex';
import CalcButton from './components/CalcButton.vue';
import CalcTotal from './components/CalcTotal.vue';

export default {
    components: { ButtonRow, CalcButton, CalcTotal },
    methods: {
        ...mapMutations({
            clear: 'clear',
            addSign: 'addSign',
            equals: 'equals',
        }),
    }
}
</script>

<style lang="scss">
    .wrapper{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .calculator{
        width: 210px;
        border-radius: 10px;
        border: 3px solid rgba(0,0,0, 0.3);
        background: rgba(0,0,0,0.1);
        padding: 10px;
        &__clear{
            width: 53%;
        }
        &__end{
            width: 100%;
        }
        &__top{
            margin-bottom: 10px;
        }
        &__between{
            margin-bottom: 10px;
            display: flex;
            &-el{
                &:not(:last-of-type){
                    margin-right: 10px;
                }
            }
        }
        &__bottom{
            display: flex;
            width: 100%;
            &-left{
                width: 55%;
                margin-right: 10px;
            }
            &-right{
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                gap: 10px;
                width: 100%;
            }
            &-el{
                &:not(:last-of-type){
                    margin-bottom: 10px;
                }
            }
            &-long{
                button{
                    width: 100%;
                }
            }
        }
    }
</style>