import { createStore } from 'vuex'

export default createStore({
  state: () => ({
    total: '',
    totalNumber: 0,
    firstNumber: {
      text: '',
      number: 0,
    },
    secondNumber: {
      text: '',
      number: 0,
    },
    isSecond: false,
    sign: '',
  }),
  getters: {
    
  },
  mutations: {
    change(state, newVal){
      state.total += String(newVal);

      if(!state.isSecond){
        state.firstNumber.text += String(newVal);
        state.firstNumber.number = Number(state.firstNumber.text);
      } else {
        state.secondNumber.text += String(newVal);
        state.secondNumber.number = Number(state.secondNumber.text);
        

        switch (state.sign){
          case '+':
            state.totalNumber = state.firstNumber.number + state.secondNumber.number;
            break;
          case '-':
            state.totalNumber = state.firstNumber.number - state.secondNumber.number;
            break;
          case '*':
            state.totalNumber = state.firstNumber.number * state.secondNumber.number;
            break;
          case '/':
            state.totalNumber = state.firstNumber.number / state.secondNumber.number;
            break;
        }
      }
    },
    addSign(state, sign){
      if(state.total == ''){
        state.total = '0';
        state.firstNumber= {
          number: 0,
          text: '0',
        }
      }

      state.total = state.firstNumber.text + ' ' + sign + ' ' + state.secondNumber.text;
      state.isSecond = true;
      state.sign = sign;
    },
    clear(state){
      state.total = '';
      state.secondNumber = {
        text: '',
        number: 0,
      };
      state.firstNumber = {
        text: '',
        number: 0,
      };
      state.totalNumber = 0;
      state.isSecond = false;
      state.sign = '';
    },
    equals(state){
      
      if(state.secondNumber.number == 0 && state.sign == '/'){
        state.total = 'Ошибка, на 0 делить нельзя';
        state.firstNumber = {
          text: 0,
          number: ''
        };
      } else {
        state.total = String(state.totalNumber);
        state.firstNumber = {
          text: state.total,
          number: Number(state.total)
        };
      }

      state.secondNumber = {
        text: '',
        number: 0,
      };
      state.sign = '';
    }
  },
  actions: {
  }
})
